:root {
    --black: #040505;
    --gray-tle-border: #EBEBEB;
}

@font-face {
    font-family: 'Сy Grotesk';
    src: local('Grotesk Wide Regular'), local('GroteskWideRegular'),
        url('../../fonts/GroteskWideRegular.woff2') format('woff2'),
        url('../../fonts/GroteskWideRegular.woff') format('woff'),
        url('../../fonts/GroteskWideRegular.ttf') format('ttf');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Cera Pro';
    src: local('CeraPro-Regular'),
        url('../../fonts/CeraPro-Regular.woff2') format('woff2'),
        url('../../fonts/CeraPro-Regular.woff') format('woff'),
        url('../../fonts/CeraPro-Regular.ttf') format('ttf');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

body {
    overflow-x: hidden;
    min-height: 100vh;
    min-height: -webkit-fill-available;
    background-color: #fff;
}

body {
    font-family: 'Сy Grotesk', sans-serif;
}

.container {
	max-width: 1224px;
	width: 100%;
    margin-left: auto;
    margin-right: auto;
}

.main_title {
	font-family: 'Сy Grotesk', sans-serif;
	font-size: 48px;
	font-weight: 400;
	line-height: 100%;
	letter-spacing: 0.01em;
	text-align: left;
    color: var(--black);
}

h1, h2, h3, h4, h5, h6,
p, div, a, ul li, ol li,
textarea, input, select, label,
span, em, b, i, :before, :after {
    color: var(--black);
}

.main-hdr {
    margin-top: 60px;
}

.main-hdr .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/**
* Burger Menu
*/

.burger-lines-wrap {
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: absolute;
    right: 0; /* if from body - calc((100vw - 1224px) / 2); */
    top: 0; /* if from body - 60px / 2); */
    z-index: 249;
}

.burger-lines {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    gap: 8px;
}

.burger-line {
    width: 100%;
    height: 2px;
    background-color: var(--black);
}

.closemenu {
    display: none;
    margin: 0;
    transition: transform .3s ease;
    position: fixed;
    top: 60px;
}

.closemenu:hover {
    transform: translateZ(180deg);
    cursor: pointer;
}

.burger-lines-wrap.active .closemenu {
    display: block;
}

.burger-lines-wrap.active .burger-lines {
    display: none;
}

/** Footer Section **/

#feedback {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    position: relative;
    background-color: #fff;
}

#mainpage-bottom-header {
    margin-bottom: 182px;
}

.feedback_main {
    max-width: 90%;
    margin: auto;
}

.feedback_get_i_t {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 30px;
}

.feedback_get_i_t_dash {
    width: 45px;
    height: 1px;
    background-color: #D8D8D8;
}

.feedback_get_i_t_label {
    font-family: 'Cera Pro';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    letter-spacing: 0.01em;
}

.feedback-heading {
    font-family: 'Сy Grotesk', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 67px;
    line-height: 150%;
    letter-spacing: 0.01em;
    color: #040505;
    padding-bottom: 18px;
    margin-bottom: 230px;
    position: relative;
}

.feedback-heading:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 2px;
    background-color: #e5e5e5;
}

.feedback-heading:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 2px;
    background-color: var(--black);
    transform: scaleX(0);
    transition: transform .4s ease;
}


.feedback-heading:hover:after {
    transform: scaleX(1);
}

.main_footer {
    margin-bottom: 70px;
}

.footer-grid {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.copyright-item, .copyright-item a {
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    letter-spacing: 0.01em;
    color: var(--black);
    transition: color .3s ease;
}

.copyright-item a {
    color: #888888;
    text-decoration: none;
}

.copyright-item a:hover {
    color: var(--black);
}   

.footer-contacts {
    display: flex;
    flex-direction: column;
    gap: 25px;
}

.footer-links ul {
    display: flex;
    align-items: center;
    gap: 26px;
    flex-wrap: wrap;
    list-style-type: none;
}

.footer-email {
    text-align: right;
}

.footer-email-link {
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 39px;
    line-height: 100%;
    letter-spacing: 0.01em;
    color: #040505;
    text-decoration: none;
    position: relative;
}

.footer-email-link:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: -3px;
    height: 1px;
    background-color: #000;
    transform: scaleX(0);
    transform-origin: right center;
    transition: transform .4s, transform-origin 0s;
}

.footer-email-link:hover:before {
    transform: scaleX(1);
    transform-origin: left center;
}

.footer-links ul li a {
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 100%;
    letter-spacing: 0.01em;
    color: transparent;
    text-decoration: none;
    position: relative;
}

.footer-links ul li a:before {
    content: attr(data-text);
    color: var(--black);
    position: absolute;
    top: 0;
    left: 0;
}

.feedback-stack {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.footer-cursor-sm {
    position: absolute;
    left: 0;
    top: 0;
    will-change: transform;
    pointer-events: none;
    contain: layout style size;
    background: #000;
    color: #000;
    transform: translate(calc(-50% + 15px), -50%);
    transition: transform .2s ease-out;
}

.footer-cursor-sm:before {
    content: '';
    position: absolute;
    display: block;
    top: -24px;
    left: -24px;
    width: 48px;
    height: 48px;
    transition: transform .2s ease;
    background: #000;
    color: #000;
    border-radius: 100%;
    transform: scale(0);
}

body .mf-cursor.-inverse-scaled {
    mix-blend-mode: exclusion;
    background-color: #fff;
    color: #fff;
}

body .mf-cursor.-inverse-scaled:before {
    transform: scale(1.32);
    background-color: #fff;
    color: #fff;
}

@supports (-webkit-touch-callout: none) {
    /* CSS specific to iOS devices */
    #feedback {
        min-height: -webkit-fill-available;
    }
}

@media only screen and (max-width: 1440px) {
    .menu_open_links {
        margin-top: auto;
    }

    .feedback-heading, #mainpage-bottom-header {
        margin-bottom: 140px;
    }
}